.container {
  display: flex;
  flex-direction: column;
  height: 100%;

  background-color: var(--background);
}

.page {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page img {
  width: 100px;
  cursor: pointer;
}

.container_cards {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  align-content: flex-start;
  width: 100%;
  height: 100%;
}
